.footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    color: rgb(219, 204, 204);
    background-color: rgb(44, 38, 49);
}

.section-top{
    display: flex;
    justify-content: space-around;
    padding: 14px;
    border-bottom: 1px solid rgba(231, 223, 223, 0.2);
    width: 100%;
}

.section-left{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.s_links{
    display: flex;
    align-items: center;
    margin-top: 10px;
}


a{
    text-decoration: none;
    margin: 5px;
    color: rgb(219, 204, 204);
}

a:hover{
    color:rgb(235, 225, 225);
}


@media screen and (max-width: 480px){
    .section-top{
        display: flex;
        flex-direction: column;
        text-align: center;
    }

    .delaklo{
        text-align: center;
        padding: 20px 0;
    }
}